<template>
  <!-- 渐变背景色区域，默认颜色 '#345dc2' -->
  <view class="banner-bg">
   <swiper class="banner-swiper" circular autoplay :interval="4000"
   @change="swiperChange" :current="current"
   indicator-dots>
   <swiper-item class="banner-item" v-for="(item, index) in bannerList" :key="index">
   <image :src="item.imageUrl" mode="scaleToFill" class="image"/>
   </swiper-item>
   </swiper>
 
  </view>
</template>

<script>
  export default {
  
  // 接收父组件传递的数据
  props: { 
  bannerList: { // 轮播图数据
  type: Array,
  default: ()=> [
  {
  id: 1,
  imageUrl: "/static/images/banner1.jpg",
  background: "#45328c",
  advertUrl: '/pages/course/course-details'
  },
  {
  id: 2,
  imageUrl: "/static/images/banner2.jpg",
  background: "#006C00",
  advertUrl: '/pages/course/course-details'
  },
  {
  id: 3,
  imageUrl: "/static/images/banner3.jpg",
  background: "#0072B7",
  advertUrl: '/pages/course/course-details'
  }
  ],
  }
  },
  
  data() {
  return {
  current: 0, //当前所在滑块的 index ，用于刷新数据时切回第1张图
  bannerBackground: '', // 背景色
  }
 },
  
  watch: {
  },
  methods: {
  swiperChange(e) {
  // 当前所在图的index
  this.current = e.detail.current;
  //轮播图切换修改背景色
  this.bannerBackground = this.bannerList[this.current].background;
  },
  }
  
  }
</script>

